        text: 'LADO MATERNO<br><br>Minha bisav&oacute; Beatrice, nascida na Bessar&aacute;bia (Rom&ecirc;nia), em 1909 e meu bisav&oacute;, Shmuel Hersh, nascido na Ucr&acirc;nia em 1899, tiveram meu av&ocirc;, Sheldon Portman que nasceu em 1929, em Cleveland, EUA.<br><br>Meu bisav&ocirc;, Shmuel Hersh, imigrou para os EUA em 1921, com toda fam&iacute;lia.<br><br>Minha bisav&oacute;, Beatrice, fugiu da Bessar&aacute;bia com 2 anos e junto com sua m&atilde;e Vieram para os EUA, deixando toda fam&iacute;lia l&aacute;.<br><br>Minha av&oacute; Marion Gumprecht (casada com meu avo Sheldon Port-man), nasceu na Alemanha em 1932. Filha de Edith, nascida em 1910 e de Wemer, nascido em 1905, ambos na Alemanha.<br><br>Em 1941, devido ao nazismo, a fam&iacute;lia imigrou para os EUA. Minha m&atilde;e, Lauren Portman nasceu na Calif&oacute;rnia (EUA), em 1963. Foi para Israel quando tinha 24 anos e conheceu meu pai Isaac Ben-roubi em 1990; dois anos mais tarde vieram para o Brasil. <br><br><br>LADO PATERNO<br><br>Meu bisav&oacute;, Isaac Benroubi, nascido em Israel e sua esposa, Bahie Ades nascida na S&iacute;ria tiveram meu av&ocirc;, Soly Benroubi, que nasceu no Egito em<br>1930. Tornou-se &oacute;rf&atilde;o ainda jovem e foi morar com seus tios e primos. <br><br>O governo eg&iacute;pcio de Nasser (1957) decretou a expuls&atilde;o dos judeus, que tiveram prazo de uma semana para sair. Nesta mesma semana Soly casou-se com Emy, filha de Shmuel Asher e Nechama Mary, ambos nascidos no Egito. Vieram para o Brasil, onde tinham parentes. Tiveram 3 filhos. Meu pai foi um deles e nasceu em 1962.<br><br>Em 1980, por motivos financeiros, toda fam&iacute;lia mudou-se para os EUA.<br><br>Em 1987 meu pai foi para Israel, onde conheceu minha mie. Em 1992 voltaram para o Brasil.',
