        id: 3,
        name: 'Sarah Scheneiderman',
        text: 'Minha tatarav&oacute; materna Mania Kleinman, seu marido Shulam Kleinman e o filho Shlomo vieram da Bessar&aacute;bia (Rom&ecirc;nia) para o Brasil em 1916, durante a Primeira Guerra, pois l&aacute; havia s&eacute;rios problemas de antissemitismo.<br><br>Eles moravam no Rio de Janeiro, onde meu tatarav&ocirc; organizou grupos de ajuda aos imigrantes judeus e os levava para casas de fam&iacute;lias judaicas, onde ficavam at&eacute; poderem morar de forma independente.<br><br>Era um homem com muita cultura, pois gostava de estudar livros de Torah e lia jornais que vinham da Argentina e Nova York.<br><br>Ele e um grupo de amigos fundaram a escola &quot;Shulam Malechem&quot; em Vila Isabel, no Rio de Janeiro, uma biblioteca e a escola &quot;Bialik&quot; no bairro do Meier. <br>Ele teve mais tr&ecirc;s filhos um dos quais era a m&atilde;e do meu av&ocirc; Sioma.',
