const getParticipantsFromGalery = (id) => {
  return data[id];
};

const getMainText = () => {
  return "A Exposição Memória & Passado tem como objetivo  retratar o impacto do êxodo na vida de famílias  judaicas imigrantes, que vivem hoje na cidade de  São Paulo. </br></br>  A exposição será montada a partir de fotografias de  familia e de materiais pessoais de familias que  sofreramo éxodo e se remodelaram com ele. As  fotografia buscam retratar as histórias de famílias  hoje, e, por meio de representações artísticas a  leitura que as próprias famílias fazem de si, tendo  em conta o êxodo e o passado. As jovens dasfamilias participantes do processo de construção eda exposição passaram por oficinas e aulas sobre  cultura, história e pesquisa para poderem buscar os  pessoais das histórias das famílias que comporiam  os temas desta exposição. </br></br> Assim, não tratamos de um acervo de importância  ou pretensão acadêmicas, contudo, a sua  implementação e a exposição das obras tocam  aspectos importantes de relevância cultural, social  e histórica. Desta forma, a exposição foi construída  procurando valorizar os conteúdos de identidade cultural, história e memória, e para evidenciar as  conexões entre as histórias de éxodo apresentadas.O viés 'visão de minoria cultural', que permeia toda  a exposição, foi também o guia artístico da  formação das oficinas das jovens e tem importância  social e cultural, além da artística, para trazer a  visão das jovens deste grupo social ao nivel  plástico.";
};

