<template lang="">
    <div class="relative bg-secondary bg-opacity-20 overflow-hidden px-4 py-4 sm:px-0 sm:py-0">
        <Triangle class="absolute h-[50vw] md:h-[80%] top-1/2 -translate-y-1/2 -translate-x-1/2 opacity-70"/>
        <Square class="absolute h-[30vw] md:h-[20vw] max-h-[516px] right-0 bottom-0 translate-x-1/2 xl:-translate-x-1/3 opacity-60 rotate-[65deg]"/>
        <div class="my-4 sm:my-0 sm:absolute top-4 left-4 cursor-pointer text-gray-800 font-semibold py-2 px-4">
            <a @click="$router.go(-1)" class="flex items-center">
                <Arrow class="!text-primary"/>
                Voltar
            </a>
        </div>
        <div class="w-full min-h-screen sm:h-screen flex justify-around items-center flex-col">
            <Splide
                class="w-full sm:w-[40vw] px-14 py-5"
                :options="{ rewind: 'true', gap: 40, autoWidth: true, pagination: false}"
                aria-label="item.title">
                <SplideSlide
                    class="cursor-pointer"
                    v-for="participant in participants.participants"
                    :key="participant.id"
                    @click="showDetail(participant)">

                    <div :class="{'text-white' : currentParticipant == participant}" class="bg-secondary p-2 text-center">
                        {{ participant.name }}
                    </div>
                </SplideSlide>
            </Splide>
            <div 
                v-if="currentParticipant"
                :id="currentParticipant.id"
                class="w-full h-full flex flex-col sm:flex-row"
                >
                <Splide
                    ref="secondary"
                    @splide:move="saveIndex()"
                    @splide:mounted="saveIndex()"
                    class="sm:w-[50vw] h-full"
                    :options="{ rewind: true, perPage: 1, resetProgress: true }"
                    aria-label="item.title font-bold">
                    <SplideSlide
                        v-for="item in currentParticipant.gallery"
                        class="cursor-pointer h-full"
                        :key="item.image">
                        <div class="flex justify-start items-center flex-col sm:flex-row">
                                <img
                                    class="sm:w-[50vw] h-[80vh] object-contain bg-[#070707d3]"
                                    :src="item.image"
                                    :alt="currentParticipant.name" />
                        </div>
                        <div class="p-4" v-html="item.text"></div>
                    </SplideSlide>
                </Splide>
                <div class="sm:w-1/2 h-full flex flex-col py-6 z-10">
                    <div class="flex flex-col px-6">
                        <p class="text-2xl font-bold mb-6 mt-3">GALERIA {{participants.title}}</p>
                    </div>
                    <div class="px-6 h-[70vh] overflow-y-scroll">
                        <div v-html="currentParticipant.text" class="text-justify" ></div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import { Splide, SplideSlide } from '@splidejs/vue-splide';
import { getParticipantsFromGalery } from '@/services/participants';

import Square from '@/assets/svgs/Square.vue';
import Triangle from '@/assets/svgs/Triangle.vue';
import Arrow from '@/assets/svgs/Arrow.vue';

export default {

	components: {
        Splide,
        SplideSlide,
        Square,
        Triangle,
        Arrow
    },

    props: {
        id: String
    },

    created() {
        this.participants = getParticipantsFromGalery(this.$route.params.id);
        this.currentParticipant = this.participants.participants[0];
    },

    computed: {
        
    },

    data() {
        return {
            participants: [],
            currentParticipant: {},
            currentPhoto: ''
        }
    },

    methods: {
        showDetail(participant) {
            console.log(this.$refs.secondary.splide.go(0));
            this.currentParticipant = participant;
        },
        saveIndex() {
            this.currentPhoto = this.$refs.secondary.splide.index;
        },
        getParticipantsFromGalery
    }
    
}
</script>

<style scoped>
    /* ===== Scrollbar CSS ===== */
  /* Firefox */
  * {
    scrollbar-width: auto;
    scrollbar-color: #343f64 #ffffff;
  }

  /* Chrome, Edge, and Safari */
  *::-webkit-scrollbar {
    width: 10px;
  }

  /* *::-webkit-scrollbar-track {
    background: #ffffff;
  } */

  *::-webkit-scrollbar-thumb {
    background-color: #343f64;
    border-radius: 10px;
    border: 2px solid #ffffff;
  }
</style>